import axios from 'axios';
import React, { useState } from 'react'
import { 
  ref,
  deleteObject }
from "firebase/storage";
import { storage } from "../firebase";



const Details = (props) => {
    const data = Object.values(props);

    const [nombre, setNombre] = useState('')
    const handleChange = (e)=>{
      setNombre(e.target.value)
      console.log(nombre)
    }

    
  const handleDelete = (e, realName) => {
    const desertRef = ref(storage, 'images/' + realName);
    deleteObject(desertRef).then(() => {
      window.location.reload()
    }).catch((error) => {
    });

  }


  //Descargar imagenes//
  const handleDownload = (e, url, name) => {
    axios({
      url: url,
      method: "GET",
      headers: {
        'content-type': 'multipart/form-data'
      },
      responseType: "blob", // importante
    }).then((response) => {
      console.log(response)
      const url = window.URL.createObjectURL(new Blob([response.data], { type: 'image' }));
      const link = document.createElement("a");
      link.href = url;
      link.download = name
      // link.setAttribute("download");
      document.body.appendChild(link);
      link.click();
    });

  }
    //END - Descargar imagenes//

  return (
    <div className="modal-dialog">
    {
        data.map((url) => {
          return <div className="modal-content">
              {/* <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button> */}
            <div className="modal-header row">
                  <div className="col-6">
                   <i class="fa-solid fa-trash-can btn" onClick={(e) => handleDelete(e, url.entireName)}></i>
                  </div>
                  <div className="col-6 d-flex flex-row-reverse">
                  <i class="fa-solid fa-xmark btn px-1" data-bs-dismiss="modal"></i>
                    <i class="fa-solid fa-pen-to-square btn px-1"></i>
                    <i class="fa-solid fa-share-nodes btn px-1"></i>
                    <i class="fa-solid fa-download px-1 btn" onClick={(e) => handleDownload(e, url.url, url.nombre)}> </i>

                  </div>
            </div>
            <div className="modal-body">

            
              <img src={url.url} alt="blueboy" className='modal-pic' />
              <p>Nombre de archivo: {url.nombre}</p>
              <p>Codigo: {url.codigo}</p>
              <p>Modelo: {url.modelo}</p>
              <p>Color: {url.color}</p>
              <p>Tags: {url.tags}</p>


                {/* <div className="form">
                    <div className="row">
                      <div className="col-md-6">
                        <label className='form-label mt-2'>Nombre:</label>
                        <input type="text"  onChange={e => {handleChange(e)}} value={url.nombre} className='form-control' />

                        <label className='form-label mt-2'>Codigo:</label>
                        <input type="text" className='form-control' />
                      </div>

                      <div className="col-md-6">
                        <label className='form-label mt-2'>Modelo:</label>
                        <input type="text"  className='form-control' />

                        <label className='form-label mt-2'>Color:</label>
                        <input type="text" className='form-control' />

                      </div>
                    </div>

                  </div> */}
              </div>
            </div>
        })
    }
    </div>  

  )
}

export default Details