import React, { useState } from 'react';
import { TagsInput } from "react-tag-input-component";
import {storage} from '../firebase'
import {
  ref,
  uploadBytes,
  updateMetadata,
  getDownloadURL,
} from "firebase/storage"



const UploadFile = () => {

  const [nombre, setNombre] = useState('')      // Use State De formulario para agregar metadato
  const [codigo, setCodigo] = useState('')      // Use State De formulario para agregar metadato
  const [modelo, setModelo] = useState('')      // Use State De formulario para agregar metadato
  const [color, setColor] = useState('')        // Use State De formulario para agregar metadato
  const [selected, setSelected] = useState([]); // Use State Para almacenar Tags
  const [file, setFile] = useState(null)        // Use State para mostrar la imagen cuando se agrega
  const [image, setImage] = useState(null);     // Use State Para enviar la imagen a Firebase
  const [url, setUrl] = useState([])            // Use State Para agregar la url de la imagen como metadato
    
  //Funcion para Mostrar la imagen cuando se carga  
  const handleChange = (e) => {
    setFile(URL.createObjectURL(e.target.files[0]))
    setImage(e.target.files[0])
  }
    
  // Enviar a Firebase
  const handleSubmit = () => {
    const imageRef = ref(storage, `images/${image.name}`);
    uploadBytes(imageRef, image).then((snapshot)=>{
      getDownloadURL(snapshot.ref).then((url)=>{
        setUrl((prev) => [...prev, url])
        const forestRef = ref(storage,`images/${image.name}` )

        const entireName = image.name
        const date = image.lastModified
        
        const formato = image.type
        const link = url.toString();
        const tags = selected.toString().toLowerCase()
        const keywords = tags + "," + nombre + "," + codigo + "," + modelo + "," + color + "," + formato;
        const metadata ={
          customMetadata:{
            "tags" : tags,
            'nombre': nombre,
            'codigo': codigo,
            'modelo': modelo,
            'color' : color,
            'url': link,
            'keywords' : keywords,
            'entireName' : entireName,
            'lastModified' : date,
            'formato' : formato,
          },
        }
        // console.log(metadata)
          updateMetadata(forestRef, metadata).then((metadata)=>{
            window.location.reload()
          })
      })

      })
    }
  // END PICTURE 
  
    var tags = "";
    var send ="";
    // SE OCULTA AGREGAR TAGS SI NO SE HA AGREGADO UNA IMAGEN 
    if(file){
        tags = <div className='my-4'> <TagsInput value={selected} onChange={setSelected} name="TAGS" placeHolder="TAGS"/> <em>Presiona Enter para agregar el tag</em> </div> 
    }
    // SE OCULTA BOTON ENVIAR HASTA QUE SE AGREGUE UN TAG
    if(selected.length !== 0){
        send = <div className="d-flex flex-row-reverse"> <button className="btn btn-success p-2" onClick={handleSubmit} >Enviar</button> </div>
    }

    return (
      <>
        {/* <Navigation/> */}
        <div className="modal-dialog">

          <div className="modal-content">
            <div className="modal-body">
              
              <div id="FileUpload">
                <div className="wrapper border-upload-modal">

                    <div className="text-upload text-center h4">
                    <i class="fa-solid fa-arrow-up-from-bracket text-primary h1 py-5"></i>
                    <p>Arrastra tu archivo aqui o <br/>
                    o subelo desde tu computadora<br/></p>
                    </div>

                    <div className="text-center">
                      <label for="file-upload" class="custom-file-upload btn-orange">
                        BUSCAR ARCHIVO
                      </label>
                      <input type="file" onChange={handleChange} id="file-upload" className="input-file" />
                    </div>
                    

                  <div className="image-container">
                    <img src={file} alt=" "/>
                  </div>

                  <div className="form">
                    <div className="row">
                      <div className="col-md-6">
                        <label className='form-label mt-2'>Nombre:</label>
                        <input type="text" name="" onChange={e => setNombre(e.target.value)} id="" className='form-control' />

                        <label className='form-label mt-2'>Codigo:</label>
                        <input type="text" name="" onChange={e => setCodigo(e.target.value)} id="" className='form-control' />
                      </div>

                      <div className="col-md-6">
                        <label className='form-label mt-2'>Modelo:</label>
                        <input type="text" name="" onChange={e => setModelo(e.target.value)} id="" className='form-control' />

                        <label className='form-label mt-2'>Color:</label>
                        <input type="text" name="" onChange={e => setColor(e.target.value.toLowerCase())} id="" className='form-control' />

                      </div>
                    </div>

                  </div>
                  {tags}
                  {send}
                </div>
              </div>



            </div>
          </div>


        </div>


      </>

  )
}

export default UploadFile



