import React, { useEffect, useState } from 'react'
import {
    ref,
    listAll,
    getMetadata,
  } from "firebase/storage";
import { storage } from "../firebase";
import { useAuth } from "../context/AuthContext";

const Edit = () => {

    
  const { logout, user } = useAuth(); //Use State Datos de Usuario y variable Handle para logout
  const [search, setSearch] = useState([]) //UseState para almacenar los datos de busqueda
  const [list, setList] = useState([]) //UseState para almacenar los resultados filtrados
  const [imageUrls, setImageUrls] = useState([]); //UseState para almacenar el url de cada imagen
  const [details, setDetails] = useState([]) //Use state para pasar los datos al componente Details
  const [view, setView] = useState('gridView')
  const [order, setOrder] = useState(false)

  // AUTHUSER
  const handleLogout = async () => {
    try {
      await logout();
    } catch (error) {
      console.error(error.message);
    }
  };
  // END-AUTHUSER

  //MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN
  const handleUpload = (e, details) => {
    e.preventDefault();
  }
  //END-MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN

  //OBTENER LAS LETRAS PULSADAS EN LA BUSQUEDA
  const handleChange = e => {
    setSearch(e.target.value);
    filter(e.target.value)
  }
  //END-OBTENER LAS LETRAS PULSADAS EN LA BUSQUEDA

  //FILTRAR LOS ARCHIVOS POR TAG, NOMBRE, MODELO, ETC.
  const filter = (termSearch) => {
    var searchResults = list.filter((item) => {
      var tag = item.keywords

      if (tag.toString().toLowerCase().includes(termSearch.toLowerCase())) {
        return tag;

      }
    })
    setImageUrls(searchResults)
  }
    //FILTRAR LOS ARCHIVOS POR TAG, NOMBRE, MODELO, ETC.





  //END-MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN

    //OBTENER DATOS DESDE FIREBASE
    const imagesListRef = ref(storage, "images/");
    useEffect(() => {

        listAll(imagesListRef).then((response) => {
          response.items.forEach((item) => {
            
            const forestRef = ref(storage, item._location.path_)
            
    
              getMetadata(forestRef).then((newMetadata)=>{

              setList((prev) => [...prev, newMetadata.customMetadata]);
              setImageUrls((prev) => [...prev, newMetadata.customMetadata]);

              
      
              });
    
          });
        });

    }, []);
    //END-OBTENER DATOS DESDE FIREBASE






  
    return (
    <>
            {/* <!-- BLOCK 1 --> */}

<nav class="navbar navbar-expand-lg navbar-light bg-light ">
  <a class="navbar-brand margin-navbar" href="#">LOGO</a>
  <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
    aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="container">
    <div class="form-inline d-flex my-2 my-lg-0">
      <a class="text-primary px-3 pt-2"><i class="fa-solid fa-magnifying-glass"></i></a>
      <input class="form-control form-control-search mr-sm-2" type="search" placeholder="Search" aria-label="Search" onChange={handleChange} value={search}/>
    </div>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <a class="nav-link text-primary" onClick={(e) => handleUpload(e)} data-bs-toggle="modal" data-bs-target="#exampleModal2">
            <i class="fa-solid fa-cloud-arrow-up h3"></i><p class="icons-text"> Upload</p>
          </a>
        </li>
 
        <div className="dropdown">
        <a class="nav-link text-primary" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            <i className="fa-solid fa-user h3"></i> <p class="icons-text"> Perfil</p>
        </a>
          <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <p className="dropdown-item disabled">{user.displayName || user.email}</p>
            <a className="nav-link btn btn-light" onClick={(e) => handleUpload(e)} data-bs-toggle="modal" data-bs-target="#exampleModal2"> <i className="fa fa-cloud-arrow-up text-secondary"></i> Subir Imagen</a>
            <li><hr className="dropdown-divider" /></li>
            <button className="dropdown-item" onClick={handleLogout}> <i className="fa-solid fa-arrow-right-from-bracket"></i> Logout </button>
          </div>
        </div>
      </ul>
    </div>
  </div>
  <hr/>
</nav>


{/* <!-- END BLOCK 1 --> */}

{/* <!-- BLOCK 2 --> */}
  <div class="row tags-container">
    
    <div class="margin-tags d-flex pt-3 ">
      <i class="fa-solid fa-tag mx-2 h4 text-secondary"></i>
      <div class="tag">
        SOFAS
      </div>
      <div class="tag">
        CHAIRS
      </div>
      <div class="tag">
        EXPO
      </div>
    </div>
  </div>
  <hr/>
{/* <!-- END BLOCK 2 -->*/}

{/* <!-- BLOCK 3 --> */}
  <div class="row filters-container">
    <div class="margin-filters d-flex ">
      <i class="fa-solid fa-filter mx-2 h4 text-secondary"></i>
      <div class="container filter">
        <div class="form-group d-flex">
          <select class="form-control form-select mx-2" aria-label="Default select example">
            <option >TIPO</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
          </select>
  
          <select class="form-control form-select mx-2" aria-label="Default select example">
            <option selected>FORMATO</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
          </select>
  
          <select class="form-control form-select mx-2" aria-label="Default select example">
            <option selected>NOMECLATURA</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
          </select>
        </div>
      </div>
    </div>
  </div>
  <hr/>

{/* <!-- END BLOCK 3 --> */}
    </>
  )
}

export default Edit

