import React, { useEffect, useState } from 'react'
import {
    ref,
    listAll,
    getMetadata,
  } from "firebase/storage";
import { storage } from "../firebase";
import Details from './Details';
import { useAuth } from "../context/AuthContext";
import UploadFile from './UploadFile';
import axios from 'axios';
import Edit from './Edit';
import Logo from '../assets/BBS_LOGO.png'






const Home = () => {

    
    const { logout, user } = useAuth(); //Use State Datos de Usuario y variable Handle para logout
    const [search ,setSearch] = useState([]) //UseState para almacenar los datos de busqueda
    const [list, setList] = useState([]) //UseState para almacenar los resultados filtrados
    const [imageUrls, setImageUrls] = useState([]); //UseState para almacenar el url de cada imagen
    const [details, setDetails] = useState([]) //Use state para pasar los datos al componente Details
    const [view, setView] = useState('gridView') 
    const [order, setOrder] = useState(false)
    const [searchTag, setsearchTag] = useState([])

    const colores = ["rojo", "colorado", "carmesí", "rubí", "rubi", "frutilla", "bermellón", "bermellon", "escarlata", "granate", 'burdeos', "carmin", "amaranto", "verde", "chartreuse", "kelly", "esmeralda", "verdeagua", "xanadu", "arlequin", "arlequín", "jade", "verones", "espárrago", "esparrago", "oliva", "cazador", "militar", "azul", "cobalto", "marino", "petroleo", "zafiro", "indigo", "klein", "acero", "magenta", "fucsia", "morado", "malva", "lila", "salmon", "lavanda", "rosa", "cian", "turquesa", "celesta", "ceruleo", "menta", "aguamarina", "amarillo", "lima", "oro", "dorado", "ambar", "topacio", "marron", "pardo", "cafe", "chocolate", "caqui", "ocre", "siena", "violeta", "lavanda", "amatista", "purpura", "naranja", "zanahoria", "sesamo", "beis", "beige", "durazno", "piel", "blanco", "nieve", "lino", "hueso", "marfil", "plata", "plateado", "zinc", "gris", "negro"]

    // AUTHUSER
    const handleLogout = async () => {
      try {
        await logout();
      } catch (error) {
        console.error(error.message);
      }
    };
    // END-AUTHUSER

    const handleView = (e, view) => {
      e.preventDefault()
      setView(view)
    }

    // AGREGAR TAGS DE BUSQUEDA
    const handleKeyDown = (e) => {
      if(e.key !== 'Enter') return
      const value = e.target.value
      if(!value.trim()) return 
      setsearchTag([...searchTag, value])
      e.target.value = ''
      tagFilter([...searchTag, value])
    
    }
    //END -- AGREGAR TAGS DE BUSQUEDA

    // ELIMINAR TAGS DE BUSQUEDA
    const removeTag = (index) =>{
      setsearchTag(searchTag.filter((el, i)=> i !== index))
      var newList = searchTag.filter((el, i)=> i !== index)
      tagFilter(newList)
    }
    //END ELIMINAR TAGS DE BUSQUEDA

    //FILTRAR LOS ARCHIVOS POR TAG
    const tagFilter = (tag) =>{
      // console.log(tag)
      setImageUrls([])
      if(tag == ""){
        showAll()
      }
      else{
        list.map(item=>{
          tag.map(userTag => {
            if( item.tags.includes( userTag ) ){
              setImageUrls((prev) => [...prev, item])
            }
    
          })
        })
      }
    }

    
    //FILTRAR LOS ARCHIVOS POR TAG

    //MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN
    const handleDetails = (e, details) =>{
        e.preventDefault();
        setDetails(details)
      }
    //END-MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN

  //MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN
  const handleUpload = (e, details) => {
    e.preventDefault();
  }
  //END-MOSTRAR EL MODAL CON LOS DETALLES DE LA IMAGEN

    //OBTENER DATOS DESDE FIREBASE
    const imagesListRef = ref(storage, "images/");
    useEffect(() => {

        listAll(imagesListRef).then((response) => {
          response.items.forEach((item) => {
            
            const forestRef = ref(storage, item._location.path_)
            
    
              getMetadata(forestRef).then((newMetadata)=>{

              setList((prev) => [...prev, newMetadata.customMetadata]);
              setImageUrls((prev) => [...prev, newMetadata.customMetadata]);

              
      
              });
    
          });
        });

    }, []);
    //END-OBTENER DATOS DESDE FIREBASE

    //OBTENER LAS LETRAS PULSADAS EN LA BUSQUEDA
    const handleChange= e => {
      setSearch(e.target.value);
      filter(e.target.value)
    }
    //END-OBTENER LAS LETRAS PULSADAS EN LA BUSQUEDA

    //FILTRAR LOS ARCHIVOS POR TAG, NOMBRE, MODELO, ETC.
    const filter = (termSearch) =>{
      var searchResults = list.filter((item) => {
        var tag = item.keywords

        if (tag.toString().toLowerCase().includes(termSearch.toLowerCase())){
            return tag;
            
        }
    })
      setImageUrls(searchResults)
    }
    //FILTRAR LOS ARCHIVOS POR TAG, NOMBRE, MODELO, ETC.

    //Descargar archivo//
  const handleDownload = (e, url,name) => {
    axios({
      url: url,
      method: "GET",
      headers: {
        'content-type': 'multipart/form-data'},
      responseType: "blob", // importante
    }).then((response) => {
       const url = window.URL.createObjectURL(new  Blob([response.data]));
       const link = document.createElement("a");
       link.href = url;
       link.setAttribute("download", name);
       document.body.appendChild(link);
       link.click();
    });
  }
    //END - Descargar archivo//


    //ORDENAR ALFABETICAMENTE //
  const handleAlphabetical = () => {
    function SortArray(x, y) {
      if (x.nombre < y.nombre) { return -1; }
      if (x.nombre > y.nombre) { return 1; }
      return 0;
    }
    imageUrls.sort(SortArray);
    setOrder(true)
  }
   //END - ORDENAR ALFABETICAMENTE //

       //ORDENAR FECHA //
  const handleDate = () => {
    function SortArray(x, y) {
      if (x.lastModified > y.lastModified) { return -1; }
      if (x.lastModified < y.lastModified) { return 1; }
      return 0;
    }
    imageUrls.sort(SortArray);
    setOrder(false)
  }
   //END - ORDENAR FECHA //

   const handleColors=()=>{
    setImageUrls([])
    list.map(item=>{
      colores.map(color => {
        if( item.keywords.includes( color ) ){
          setImageUrls((prev) => [...prev, item])
        }

      })
    })

  }

  const handleJPG =()=>{
    setImageUrls([])
    list.map(item=>{
        if( item.keywords.includes( "image/jpeg" ) ){
          setImageUrls((prev) => [...prev, item])
        }

    })
  }

  const handlePNG =()=>{
    setImageUrls([])
    list.map(item=>{
        if( item.keywords.includes( "image/png" ) ){
          setImageUrls((prev) => [...prev, item])
        }


    })
  }

  const showAll = () => {
    listAll(imagesListRef).then((response) => {
      setImageUrls([])
      response.items.forEach((item) => {

        const forestRef = ref(storage, item._location.path_)


        getMetadata(forestRef).then((newMetadata) => {

          // setList((prev) => [...prev, newMetadata.customMetadata]);
          setImageUrls((prev) => [...prev, newMetadata.customMetadata]);


        });

      });
    });
  }
      

    var selectedView = '';

    var gridView =imageUrls.map((url) => {
      return<div class="col-lg-2 col-md-3">
          <div class="card">
            <div class="card-body" onClick={(e) => handleDetails(e, url)} data-bs-toggle="modal" data-bs-target="#exampleModal">
              <h5 class="card-title">{url.nombre}</h5>
              <div class="picture-container">
                <img src={url.url} alt="blueboy" class="picture" />
              </div>
            </div>
                <button class="btn btn-dark download-jpg" onClick={(e) => handleDownload(e, url.url, url.nombre)}>JPG <i class="fa-solid fa-arrow-down"></i></button>
          </div>
          <div class="codigo">CODIGO: {url.codigo}</div>
          <div class="modelo mb-4">MODELO: {url.modelo}</div>
        </div> 
      })

    var listView = <table class='table table-bordered text-center'>
    <thead> 
      <tr> 
        <th>Imagen</th> 
        <th class="">Detalles</th>
        <th class="list-hidden">Nombre</th> 
        <th class="list-hidden">Codigo</th> 
        <th class="list-hidden">Modelo</th>
        <th class="list-hidden">Tags</th> 
      </tr> 
    </thead>
    <tbody> 
    {imageUrls.map((url) => {
    return <tr >
        <th className="picture-list" ><img src={url.url} alt="imagen"  /></th> 
        <td class="details" onClick={(e) => handleDetails(e, url)} data-bs-toggle="modal" data-bs-target="#exampleModal"> <p>Detalles</p></td>
        <td class="list-hidden"><p>{url.nombre}</p></td> 
        <td class="list-hidden"><p>{url.codigo}</p></td> 
        <td class="list-hidden"><p>{url.modelo}</p></td> 
        <td class="list-hidden"><p>{url.tags}</p></td> 
      </tr>
    })}
    </tbody>
    </table>

    if(view === 'listView'){
        selectedView = listView;
    }
    else{
      selectedView = gridView;
    }
  
    return (
    <>
      {/* <!-- BLOCK 1 --> */}

<nav class="navbar navbar-expand-lg navbar-light bg-light ">
  <a class="navbar-brand margin-navbar" href="#"><img src={Logo} alt="LOGO" /></a>
  <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
    aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="container">
    <div class="form-inline d-flex my-2 my-lg-0">
      <a class="text-primary px-3 pt-2"><i class="fa-solid fa-magnifying-glass"></i></a>
      <input class="form-control form-control-search mr-sm-2" type="search" placeholder="Search" aria-label="Search" onChange={handleChange} value={search}/>
    </div>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <a class="nav-link text-primary hover-icon" onClick={(e) => handleUpload(e)} data-bs-toggle="modal" data-bs-target="#exampleModal2">
            <i class="fa-solid fa-cloud-arrow-up h3"></i><p class="icons-text"> Upload</p>
          </a>
        </li>
 
        <div className="dropdown">
        <a class="nav-link text-primary hover-icon" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            <i className="fa-solid fa-user h3"></i> <p class="icons-text"> Perfil</p>
        </a>
          <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <p className="dropdown-item disabled">{user.displayName || user.email}</p>
            <a className="nav-link btn btn-light" onClick={(e) => handleUpload(e)} data-bs-toggle="modal" data-bs-target="#exampleModal2"> <i className="fa fa-cloud-arrow-up text-secondary"></i> Subir Imagen</a>
            <li><hr className="dropdown-divider" /></li>
            <button className="dropdown-item" onClick={handleLogout}> <i className="fa-solid fa-arrow-right-from-bracket"></i> Logout </button>
          </div>
        </div>
      </ul>
    </div>
  </div>
  <hr/>
</nav>


{/* <!-- END BLOCK 1 --> */}


{/* <!-- BLOCK 2 --> */}
  <div class="row tags-container container">
    <div class="margin-tags d-flex pt-3 flex-wrap">
      <i class="fa-solid fa-tag mx-2 pt-3 h4 text-secondary tag-icon"></i>
      <input onKeyDown={handleKeyDown} type="text" className='busqueda-tag form-control' placeholder='Escribe un tag' />
      {searchTag.map((tag, index)=>(<div class="tag" key={index}> {tag.toUpperCase()} <span onClick={()=>removeTag(index)} className="close">x</span></div>))}
    </div>
  </div>
  <hr className='list-hidden'/>
{/* <!-- END BLOCK 2 -->*/}

{/* <!-- BLOCK 3 --> */}
        <div class="row filters-container">
          <div class="margin-filters d-flex ">
            <i class="fa-solid fa-filter mx-2 h4 text-secondary"></i>
            <div class="container filter">
              <div class="form-group d-flex">

                <div class="dropdown mx-2">
                  <button class="btn dropdown-toggle" type="button" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    TIPO
                  </button>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a class="dropdown-item" onClick={showAll}>Todos</a>
                    <a class="dropdown-item" onClick={handleColors}>Colores</a>
                  </div>
                </div>

                <div class="dropdown mx-2">
                  <button class="btn dropdown-toggle" type="button" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    FORMATO
                  </button>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a class="dropdown-item" onClick={showAll}>Todos</a>
                    <a class="dropdown-item" onClick={handleJPG}>JPG</a>
                    <a class="dropdown-item" onClick={handlePNG}>PNG</a>
                  </div>
                </div>


              </div>
            </div>
          </div>
        </div>
  <hr className='list-hidden'/>

{/* <!-- END BLOCK 3 --> */}

{/* <!-- BLOCK 4 --> */}
    <div class="margin-sortBy">
      <div class="row justify-content-between">
        <div class="col-lg-4 col">
          <div class="icons">
            <a className='sort-icon' onClick={(e) => handleView(e, "gridView")}><i class="fa-solid fa-border-all h4 text-secondary"></i></a>
            <a className='sort-icon' onClick={(e) => handleView(e, "listView")}><i class="fa-solid fa-list h4 text-secondary"></i></a>
            <a className='sort-icon' onClick={(e) => handleView(e, "listView")}><i class="fa-solid fa-list-ul h4 text-secondary"></i></a>
          </div>
        </div>

            <div class=" d-flex pr-3 py-1 btn-order">
              <div>
                <div class="dropdown">
                  <button class="btn btn-light shadow dropdown-toggle" type="button" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    <i class="fa-solid fa-bars-staggered"></i>Ordenar Por
                  </button>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a class="dropdown-item" onClick={handleAlphabetical}><i class="fa-solid fa-arrow-down-a-z"></i> Alfabetico</a>
                    <a class="dropdown-item" onClick={handleDate}><i class="fa-solid fa-arrow-down-1-9"></i> Mas nuevas</a>
                  </div>
                </div>
              </div>
            </div>
    
      </div>
    </div>
{/* <!-- END BLOCK 4 --> */}

      <div className="mt-5">
      <div className="modal fade modal-upload" id="exampleModal2" tabIndex="-1" aria-labelledby="exampleModalLabel2" aria-hidden="true">
          <UploadFile />
      </div>
        {/* Modal Detalles */}
        <div className="modal fade come-from-modal right details-component" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <Details data={details} />
        </div>

        {/* Modal Editar */}
        <div className="modal fade" id="exampleModal3" tabIndex="-1" aria-labelledby="exampleModalLabel3" aria-hidden="true">
          <Edit data={details} />
        </div>

        <div className="viewObjects">
          <div className="row">

            {selectedView}

          </div>
        </div>
      </div>
    </>
  )
}

export default Home

